label.w-full{
  display: flex;
}

.otp-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.cell4 {
  padding: 0 13vw
}

.otp-input-hide {
  width: 0px;
  border: none;
  position: absolute;
}

.cell {
  display: flex;
  width: 13vw;
  height: 13vw;
  border-radius: 16px;
  border: 0.5px solid #E8E9EB;
  align-items: center;
  justify-content: center;
}

.cell span {
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 1;
}

.otp-placeholder {
  color: #BDBDBD !important;
  font-weight: 500 !important;
}

.cursor {
  animation: show 0.5s ease infinite ;
  font-weight: 200 !important;
}

.focused {
  border-width: 1;
  border-style: solid;
}

.otp-error {
  border-width: 1;
  border-style: solid;
}

@keyframes show {
  0%{
    transform: scale(1);
  }
  100% {
    transform: scale(0.85);
  }
}